header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  padding: 15px 0;
  z-index: 9999;
  transition: all 0.6s ease-in-out;
}
header .brand-logo img {
  height: 44px;
}
header .brand-logo .logo-black {
  display: none;
}
header .upper .number-wrapper {
  margin-right: 20px;
  margin-top: 10px;
}
header .upper .ctc-wrapper {
  margin-right: 20px;
}
header .upper .enq-btn-wrapper a {
  display: inline-block;
  padding: 0px 15px;
  color: #fff;
  background-color: #6b6b6b;
  text-transform: uppercase;
  line-height: 40px;
}
header .lower .nav-links {
  text-align: right;
}
header .lower .nav-links a {
  display: inline-block;
  margin-right: 20px;
  margin-top: 10px;
  color: #4C4C4C;
  position: relative;
}
header .lower .nav-links a:last-child {
  margin-right: 0px;
}
header .lower .nav-links a.active {
  color: #003E63;
}
header .lower .nav-links .themeBtn a {
  color: #fff;
  margin-top: 0;
}
header .form-container {
  position: absolute;
  width: 300px;
  top: calc(100% + 10px);
  right: 15px;
  background-color: #efece7;
  padding: 10px;
  display: none;
}
header .form-container .frmclose {
  display: none;
  display: inline-block;
  position: absolute;
  top: 10px;
  right: 10px;
  color: #000;
  font-size: 20px;
}
header .form-container .thankyou {
  margin-bottom: 10px;
  margin-bottom: 10px;
}

header.active {
  background-color: #fff;
  z-index: 1020;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1607843137);
}
header.active .brand-logo .white-logo {
  display: none;
}
header.active .brand-logo .logo-black {
  display: block;
}

header.headerBg {
  background-color: #fff;
  z-index: 1020;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1607843137);
}
header.headerBg .brand-logo .white-logo {
  display: none;
}
header.headerBg .brand-logo .logo-black {
  display: block;
}

.menu-icon-mobile {
  display: block;
  width: 50px;
  padding: 14px 10px 4px;
  background: linear-gradient(90deg, #003E63 0%, #0D669C 100%);
  border-radius: 6px;
  margin: 0;
  position: absolute;
  top: -5px;
  right: 15px;
  height: 40px;
}
.menu-icon-mobile > span {
  display: block;
  height: 2px;
  width: 100%;
  margin-bottom: 4px;
  background-color: #fff;
}

.menu-icon-mobile.active {
  padding-top: 18px;
}
.menu-icon-mobile.active > span:nth-child(1) {
  transform: rotate(45deg);
}
.menu-icon-mobile.active > span:nth-child(2) {
  transform: rotate(135deg);
  position: relative;
  top: -5px;
}
.menu-icon-mobile.active > span:nth-child(3) {
  display: none;
}

.call-icon svg {
  width: 20px;
  height: 20px;
  position: relative;
  right: 2px;
  top: 4px;
}

.enquire-btn {
  background-color: green;
  color: #fff !important;
  font-size: 17px;
  padding: 14px 20px;
  font-weight: 500;
  line-height: 16px;
  border-radius: 4px;
  cursor: pointer;
}

