footer {
  float: left;
  width: 100%;
  background-color: #000;
  color: #fff;
  padding: 30px 0 30px;
}
footer a {
  color: #fff;
}
footer .container .footerWrapper .partA {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0 120px;
  padding-bottom: 35px;
  border-bottom: 1px solid #5A5A5A;
}
@media (max-width:992px) {
  footer .container .footerWrapper .partA {
    flex-direction: column;
    align-items: start;
    gap: 15px 0;
  }
}
footer .container .footerWrapper .partA .content .title {
  font-weight: 650;
  font-size: 24px;
}
footer .container .footerWrapper .partA .content .para {
  font-weight: 450;
  font-size: 16px;
  line-height: 24px;
}
footer .container .footerWrapper .partA .content .footer-logo {
  width: 200px;
}
footer .container .footerWrapper .partA .mailBox .subscribeMailForm .formGroup {
  display: flex;
  font-weight: 450;
  font-size: 14px;
  color: #003E63;
}
footer .container .footerWrapper .partA .mailBox .subscribeMailForm .formGroup input::placeholder {
  font-weight: 450;
  font-size: 14px;
  color: #003E63;
}
footer .container .footerWrapper .partA .mailBox .subscribeMailForm .formGroup .emailPassage {
  padding: 13px 18px;
  padding-right: 200px;
  background-color: #fff;
  margin: 0;
  border: 0px;
  border-radius: 6px 0 0 6px;
}
@media (max-width:640px) {
  footer .container .footerWrapper .partA .mailBox .subscribeMailForm .formGroup .emailPassage {
    padding-right: 0;
  }
}
footer .container .footerWrapper .partA .mailBox .subscribeMailForm .formGroup .submitBtn {
  padding: 13px 20px;
  padding-right: 40px;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  background-color: #fff;
  border-left: 1px solid #003E63;
  border-radius: 0 6px 6px 0;
  position: relative;
}
footer .container .footerWrapper .partA .mailBox .subscribeMailForm .formGroup .submitBtn::after {
  position: absolute;
  content: url("../images/rightVector.svg");
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}
footer .container .footerWrapper .partB {
  padding: 35px 0 80px;
  display: grid;
  grid-template-columns: 30% auto;
  align-items: start;
  gap: 0 50px;
}
@media (max-width:992px) {
  footer .container .footerWrapper .partB {
    grid-template-columns: auto;
  }
}
footer .container .footerWrapper .partB .companyInfo .logo {
  margin-bottom: 15px;
}
footer .container .footerWrapper .partB .companyInfo p {
  font-size: 14px;
}
footer .container .footerWrapper .partB .companyInfo .address {
  font-weight: 450;
  font-size: 14px;
  line-height: 24px;
  margin-bottom: 20px;
}
footer .container .footerWrapper .partB .companyInfo .mail {
  font-weight: 350;
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 15px;
