.specialize-slide .specialiseArrow {
  display: flex;
  justify-content: space-between;
  width: 90%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 4;
  cursor: pointer;
}

.specialize-slide {
  position: relative;
  height: 100vh;
  overflow: hidden;
}

.specialize_slider {
  display: flex; /* put slides in a row */
  width: 100%; /* adjust to number of slides */
  height: 100%;
}

.specialize_cont {
  flex: 0 0 100%; /* each slide takes full width */
  height: 100%;
  display: flex;
  width: 80%;
  margin: 0 auto;
  justify-content: center;
  flex-direction: column;
  justify-content: center;
}

.specialise_1 {
  background: #111;
  color: #fff;
}

.specialise_2 {
  background: #444;
  color: #fff;
  overflow: hidden;
  position: relative;
  margin-left: -1px;
}

.specialise_2::after {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(279.52deg, #fdc500 2.43%, #fdc500 42.22%, #fd52ed 97.57%);
  opacity: 0;
  z-index: 0;
}

.specialise_2 > * {
  position: relative;
  z-index: 1;
}

.specialise_2.gradient-bg {
  background: linear-gradient(279.52deg, #fdc500 2.43%, #fdc500 42.22%, #fd52ed 97.57%);
}

.specialise_2 {
  --gradOpacity: 0;
}

.specialise_2::after {
  opacity: var(--gradOpacity);
}

