@keyframes mover {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-5px);
  }
}
.scroll-animate {
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.8s ease-out;
  will-change: transform, opacity;
  transition-delay: 0s;
}

.scroll-animate.animate {
  opacity: 1;
  transform: none;
  transition-delay: 0.4s;
}

/* Clip-path reveal for images */
.scroll-animate.clip-reveal {
  clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);
  transition: all 3s cubic-bezier(0.215, 0.61, 0.355, 1) !important;
}

.scroll-animate.clip-reveal.animate {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);
}

.scroll-animate.animate-out {
  opacity: 0;
  transform: translateY(20px);
  transition-delay: 0s;
}

/* Variants */
.fade-in {
  transform: none;
}

.slide-in-left {
  transform: translateX(-50px);
}

.slide-in-right {
  transform: translateX(50px);
}

.slide-in-up {
  transform: translateY(50px);
}

.slide-in-down {
  transform: translateY(-50px);
}

.scroll-animate.delay-1 {
  transition-delay: 0.4s;
}

.scroll-animate.delay-2 {
  transition-delay: 0.6s;
}

.scroll-animate.delay-3 {
  transition-delay: 0.8s;
}

.scroll-animate.delay-4 {
  transition-delay: 1s;
}

.scroll-animate.delay-5 {
  transition-delay: 1.2s;
}

.scroll-animate.delay-6 {
  transition-delay: 1.4s;
}

.scroll-animate.delay-7 {
  transition-delay: 1.6s;
}

.scroll-animate.delay-8 {
  transition-delay: 1.8s;
}

.scroll-animate.delay-9 {
  transition-delay: 2s;
}

.scroll-animate.delay-10 {
  transition-delay: 2.2s;
}

